
import Checklist from 'vux/src/components/checklist/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Checklist,
},
mounted() {
this.pageName = this.$route.query.name||'选择';
this.dictId = this.$route.query.listName||'';
},
data() {
return {
dictId:'',
value:[],
pageName:"",
list: [
{key:'0', value:"自建"},
{key:'1', value:"迈迪网注册企业"},
],
showAdd:false,
};
},
methods: {
closeConfirm(value){
console.log(value)
},
changeValue(value, label) {
console.log(value)
if (value[0] !== undefined) {
if(value[0] == this.dictId){
return false
}
this.$bus.emit("dictSelectedFrom", {
value: value[0],
label: label[0]
});
this.$router.back();
}
}
},

};



